<template>
	<view class="page">
		<view class="grid">
			<view class="item" v-for="p in pets" :key="p.id" @tap="goDetail(p.id)">
				<image class="avatar" :src="p.avatar" mode="aspectFill"></image>
				<text class="name">{{ p.name }}</text>
			</view>
		</view>
	</view>
</template>

<script setup>
import { ref } from 'vue'

const pets = ref([
	{ id: '1', name: '喵喵', avatar: '/static/logo.png' },
	{ id: '2', name: '旺财', avatar: '/static/logo.png' }
])

function goDetail() {
	uni.navigateTo({ url: '/pages/pet/detail' })
}
</script>

<style scoped>
.page { padding: 16rpx; }
.grid { display: grid; grid-template-columns: repeat(3, 1fr); gap: 16rpx; }
.item { background: #fff; border-radius: 16rpx; padding: 12rpx; text-align: center; }
.avatar { width: 140rpx; height: 140rpx; border-radius: 12rpx; background: #f5f5f5; margin: 0 auto 8rpx; }
.name { display: block; }
</style>
